.header {
    width: 100vw;
    height: 15%;
    position: fixed;
    background-image: url('data:image/jpeg;base64,');
    background-repeat: repeat-x;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    color: rgb(14, 100, 14);
}

.logo {
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
}

img {
    height: 100%;
}

h1 {
    color: #36a10bd2;
}